<template>
    <div class="container">
      <h1>转运仓（出件）库存列表</h1>
       
      <div class="tableContainer">
        <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="id"
        label="ID"
        width="80">
      </el-table-column> 
      <el-table-column
        prop="order_id"
        label="订单ID"
        width="180">
      </el-table-column>
      <el-table-column
        prop="track_company"
        label="运单号">
      </el-table-column>
      <el-table-column
        prop="sku"
        label="产品sku">
      </el-table-column>
      <el-table-column
        prop="sku"
        label="图片">
      </el-table-column>

      <el-table-column
        prop="sku"
        label="产品 ">
      </el-table-column>
<el-table-column
        prop="qty"
        label="数量">
      </el-table-column>
<el-table-column
        prop="create_date"
        label="上架时间">
      </el-table-column>
<el-table-column
        prop="expired_day"
        label="有效期">
      </el-table-column>
<el-table-column
        prop="sales"
        label="销售">
      </el-table-column>
<el-table-column
        prop="reservoir_area"
        label="物流公司">
      </el-table-column>
<el-table-column
        prop="fee"
        label="处理费">
      </el-table-column>
<el-table-column
        prop="destroy_time"
        label="弃置时间">
      </el-table-column>
<el-table-column
        prop="country"
        label="国家">
      </el-table-column>
<el-table-column
        prop="new_order_id"
        label="新订单号">
      </el-table-column>
      <el-table-column
        prop="new_order_id"
        label="新运单号">
      </el-table-column>
      <el-table-column
        prop="status"
        label="状态">
      </el-table-column>

      <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-button @click="handleEdit(scope.row)" type="text" size="small">编辑</el-button>
              <el-button @click="handleDelete(scope.row,scope.$index)" type="text" size="small">删除</el-button> 
            </template>
        </el-table-column>


      </el-table>
        
      </div>
    </div>
</template>

<script>
    
  export default {
    name: "warehouse",
    data() {
        return {
          tableData: [/*{
            "id": "1",
            "code": "AAA",
            "name": "A仓库",
            "country": "中国",
            "mobile": "12312323",
            "address": "东莞",
            "type": "自建仓"
          },
          {
            "id": "1",
            "code": "AAA",
            "name": "A仓库",
            "country": "中国",
            "mobile": "12312323",
            "address": "东莞",
            "type": "自建仓"
          },*/ 
          ],
          totalSize: 0,
          currentPage: 1,
          pageSize: 20
        }
    },
    methods: {
      filterDegree() {

      },
      filterStatus() {

      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.initTable();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.initTable();
      },
      
      addSubInventory() {
        this.$router.push('/sub-inventory/add')
      },
      handleEdit(row) {
        this.$router.push({ path: '/sub-inventory/update',query: row})
      },
      handleDetail(row) {
        this.$router.push({ path: '/sub-inventory-sku/index',query: row})
      },
      handleDelete(row,index) {
        let url = 'http://api.orkowms.me/sub-inventory/'+row.id;
        let that = this;
        this.$confirm('是否确认删除该条记录?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          that.$axios.delete(url).then((res) => {
            if(res.status == 200 || res.status == 204){
              that.tableData.splice(index,1);
              that.$message({
                type: 'success',
                message: '删除成功!'
              });
            }else {
              this.$message({
                type: 'error',
                message: '删除失败!'
              });
            }
          }).catch(() => {
            this.$message({
              type: 'error',
              message: '删除失败!'
            });
          })
        }).catch(() => {});
      },
       
      initTable() {
        let initList = [];
        let url = "http://api.orkowms.me/stocks/stock";//?page="+this.currentPage+"&per-page="+this.pageSize;
        
        let that = this;
        let config = {
          headers : { 
            'Content-Type':'application/json;charset=UTF-8'
          },
        }; 
        this.$axios.get(url,config).then((res) => {  
          if(res.data.data){ 
            let tasksList = res.data.data ;
            //that.totalSize = parseInt(res.data._meta.totalCount);
            tasksList.forEach((task) => {
              let tempTask = {};
              tempTask.id = task.id;
              tempTask.order_id = task.order_id;
              tempTask.goods_id = task.goods_id;
              tempTask.sku = task.sku;
              tempTask.qty = task.qty;
              tempTask.create_date = task.create_date;
              tempTask.expired_day = task.expired_day;
              tempTask.status = task.status;
              tempTask.fee = task.fee;
              tempTask.country = task.country;
              tempTask.sales = task.sales;
              tempTask.track_company = task.track_company;
              tempTask.track_number = task.track_number;
              tempTask.new_order_id = task.new_order_id;
              tempTask.destroy_time = task.destroy_time;
              tempTask.reservoir_area = task.reservoir_area;
              tempTask.location = task.location;
              tempTask.print_time = task.print_time;
              tempTask.outbound_time = task.outbound_time;
              
              initList.push(tempTask);
            })
          }
          that.tableData = initList; 
          console.log(res)
        }).catch((err) => {
          console.log(err)
        })
      }
    },
    created() {
      this.initTable();
    }
  }
</script>

<style lang="stylus">
  .container
    padding 30px
    position relative
    .addItem
      margin 20px 0
    .tableContainer
      position absolute
      left 30px
      right: 30px
      top: 148px
      bottom: 0
      display flex
      flex-direction column
      .table
        flex 1
        .demo-table-expand
          font-size: 0
          label
            width: 90px
            color: #99a9bf
          .el-form-item
            margin-right: 0
            margin-bottom: 0
            width: 33.333%
        td.markColor
          color #00a0e9
        a
          color #03B8CC
      .el-pagination
        text-align right
        margin 8px 0
</style>



